.list__todo .list-item {
  position: relative;
  padding: 5px 70px 5px 30px;
  margin-top: 3px;
  background-color: #f5f5f5; }

.list__todo .list-item:first-child {
  margin-top: 0; }

.list__todo .list-item .box__checkbox {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 20px;
  height: 20px; }

.list__todo .list-item input[type="checkbox"] {
  opacity: 0; }

.list__todo .list-item .label {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  border: 2px solid #4c4c4c;
  border-radius: 4px;
  background-color: #fff; }

.list__todo .list-item input[type="checkbox"]:checked + .label:before {
  content: "\f00c";
  font-family: FontAwesome;
  font-size: 15px; }

.list__todo .list-item .text__todo {
  display: block;
  min-height: 20px;
  font-size: 14px;
  word-break: break-all;
  line-height: 18px; }

.list__todo .list-item .box__button {
  position: absolute;
  top: 2px;
  right: 5px;
  font-size: 0; }

.list__todo .list-item .box__button button {
  border: 0;
  background: none;
  font-size: 20px; }

.list__todo .list-item .box__button .button__modify .fa {
  position: relative;
  top: 1px; }

.list__todo .list-item .box__button .button__complete,
.list__todo .list-item .box__button .button__cancel {
  display: none; }

.list__todo .list-item .box__button--modify .button__complete,
.list__todo .list-item .box__button--modify .button__cancel {
  display: inline-block; }

.list__todo .list-item .box__button--modify .button__modify,
.list__todo .list-item .box__button--modify .button__delete {
  display: none; }

.list__todo .list-item.list-item--done .text__todo {
  color: #4c4c4c;
  text-decoration: line-through; }

.box__input-todo {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 40px;
  padding: 5px 80px 5px 5px;
  background-color: #f5f5f5;
  border-top: 0.5px solid #d9d9d9; }

.box__input-todo .input__todo {
  width: 100%;
  height: 30px;
  padding: 0 5px;
  border: 1px solid #cacaca;
  border-radius: 2px; }

.box__input-todo .button__submit {
  position: absolute;
  top: 5px;
  right: 5px;
  width: 70px;
  height: 30px; }
